/*
 * Colors
 */

$primary: #F04E38;
$primary-dark: darken($primary, 10%);

$accent-1: #4edce9;
$accent-1-dark: darken($accent-1, 10%);

$accent-2: #f46036;
$accent-2-dark: darken($accent-2, 10%);

$accent-3: #f4d352;
$accent-3-dark: darken($accent-3, 10%);

$accent-4: #4ce1aa;
$accent-4-dark: darken($accent-4, 10%);

$black: #383F3D;
$black-60: rgba($black, 0.6);
$black-25: rgba($black, 0.25);
$black-10: rgba($black, 0.1);
$black-5: rgba($black, 0.05);

$white: #fff;
$white-60: rgba($white, 0.6);
$white-25: rgba($white, 0.25);
$white-10: rgba($white, 0.1);
$white-5: rgba($white, 0.05);

$error: #dc3545;
$info: #17a2b8;
$success: #28a745;
$warning: #ffc107;

/*
 * Shadows
 */

$shadow: 0 1px 2px rgba($black, 0.15);
$shadow-1: 0 2px 4px rgba($black, 0.15);
$shadow-2: 2px 5px 10px rgba($black, 0.15);
$shadow-3: 0 0 15px rgba($black, 0.15);

/*
 * Typography
 */

$header-font: 'Lato', sans-serif;
$paragraph-font: 'Arvo', sans-serif;

$paragraph-font-size: 16px;
$paragraph-font-weight: 400;

/*
 * Navigation
 */
$nav-breakpoint: 1100px;
$nav-breakpoint-max: $nav-breakpoint - 1;
$hamburger-width: 56px; //Used to determine how far the off canvas should slide
$off-canvas-slide-offset: $hamburger-width + 60px;


$z-index-header: 1000;
$z-index-modal: 2000;